import tuiSwipeAction from "@/components/swipe-action/swipe-action"
import tuiButton from "@/components/extend/button/button"
import tuiNumberbox from "@/components/numberbox/numberbox"
import tuiIcon from "@/components/icon/icon"
import tuiDivider from "@/components/divider/divider"
import tuiLoadmore from "@/components/loadmore/loadmore"
import {
	ajax,
	getSecret
} from '@/common/ajax/ajax.js'
export default {
	components: {
		tuiSwipeAction,
		tuiButton,
		tuiNumberbox,
		tuiIcon,
		tuiDivider,
		tuiLoadmore
	},
	data() {
		return {
			store: '',
			storeName: '总部旗舰店',
			tagIndex: 1,
			showTag: false,
			userInfo: null,
			headerPosition: "fixed",
			secret: '',
			openid: '',
			dataList: [],
			actions: [{
				name: '删除',
				color: '#fff',
				fontsize: 28,
				width: 64,
				background: '#F82400'
			}],
			isEdit: false,
			productList: [],
			pageIndex: 1,
			loadding: false,
			pullUpOn: true,
			// 购物车总数量
			count: 0,
			// 总金额
			totalMoney: 0,
			// 全选
			allchecked: false,
			// 单项：
			itemchecked: false,
			// 选择的商品
			chooseList: [],
			// 删除商品ID
			delId: '',
			act: '',
			tips: '',
			msg: '',
			// 支付金额
			allprice: 0,
			scrollTop: 0

		}
	},
	onPageScroll(e) {
		if (e.scrollTop >= 0) {
			this.headerPosition = "fixed";
		} else {
			this.headerPosition = "absolute";
		}
	},
	onLoad() {
		this.secret = uni.getStorageSync('user');			
		this.getproList()
		
	},
	onShow() {
		this.tagIndex=1
		this.store = 10001;
		this.changeData();			
		let code = uni.getStorageSync('store').store_code;			
		if (code == 10001) {
			this.showTag = false;
		} else if (typeof(code) == 'undefined') {
			this.showTag = false;
		} else {
			this.showTag = true;
		}
		uni.getStorage({
			key: 'userInfo',
			success: (res) => {
				console.log(res);
				this.userInfo = res.data;
			}
		});

	},
	methods: {
		toGood(e) {
			console.log(e);
			uni.navigateTo({
				url: '../../goods/goods?gid=' + e.proid
			})
		},
		chooseStore(e) {
			this.tagIndex = e;
			if (e == 1) {
				this.store = 10001;
				this.storeName = '总部旗舰店'
				this.changeData();
				this.productList=[];
				this.changeProlist()
			} else {
				this.store = uni.getStorageSync('store').store_code;
				this.storeName = uni.getStorageSync('store').store_name;
				this.changeData();
				this.productList=[];
				this.changeProlist()
			}

		},
		changeProlist() {
			ajax({
				url: '/Cart/guess',
				data: {
					xopenid: this.secret.openid,
					sessionKey: this.secret.sessionKey,
					secret: this.secret.secret,
					timestamp: this.secret.timestamp,
					store: this.store
				},
				method: 'post',
				success: (res) => {						
					let data = res.data.data;
					if (data.length == 0) {
						this.productList = [];
					} else {
						data.map(ele => {
							ele.pro_sn = 'http://img.xmvogue.com/thumb/' + ele.pro_sn + '.jpg?x-oss-process=style/300';
							
						});							
						if(data.length=20){
							this.productList = this.productList.concat(data);
						}else{
							this.productList=data;
						}						
					}

				},
				error: () => {}
			})
		},
		changeData() {
			ajax({
				url: '/Cart',
				data: {
					xopenid: this.secret.openid,
					sessionKey: this.secret.sessionKey,
					unionid: '',
					secret: this.secret.secret,
					timestamp: this.secret.timestamp,
					store: this.store
				},
				method: 'post',
				success: (res) => {
					let goodsList = res.data.prolist;
					this.$xm.requestImg(goodsList);
					this.dataList = goodsList;
					this.count = res.data.count
					this.totalMoney = res.data.allprice;
					this.tips = res.data.tips;
					this.getmatchOptimals();
					if (this.count !== null) {
						this.count = this.count.toString()
						uni.setTabBarBadge({
							index: 2,
							text: this.count
						})
					} else {
						uni.removeTabBarBadge({
							index: 2,
						})
					}
					uni.setStorage({
						key: 'cart',
						data: this.count,
						success: (res) => {
							console.log(res);
						}
					})
				},
				error: () => {}
			})
		},
		getuserinfo() {
			// wx登录
			uni.login({
				success: (res) => {
					if (res.code) {
						var code = res.code
						uni.getUserInfo({
							success: (res) => {
								this.userInfo = res.userInfo;
								let params = {
									encryptedData: res.encryptedData,
									iv: res.iv,
								}
								this.$xm.post('/Index/getUnionid', params, (res) => {
									uni.setStorage({
										key: 'unionid',
										data: res.unionid,
										success: (res) => {
											console.log(res);
										}
									});
								})
								uni.setStorage({
									key: 'userInfo',
									data: res.userInfo,
									success: (res) => {
										console.log(res);
									}
								});
								uni.navigateTo({
									url: '../../order/confirmation?store='+this.store
								})
							},
							fail: res => {
								// 获取失败的去引导用户授权 
							}
						})

					} else {}
				}
			})
		},
		// 获取优惠活动
		getmatchOptimals() {
			let data = {
				money: this.totalMoney
			}
			this.$xm.post('/Coupon/match_optimals', data, res => {
				this.msg = res.data.msg;
				this.allprice = res.data.money;


			})

		},
		// 获取猜你喜欢
		getproList() {
			this.$xm.post('/Cart/guess', '', (res) => {
				res.data.map(ele => {
					ele.pro_sn = 'http://img.xmvogue.com/thumb/' + ele.pro_sn + '.jpg?x-oss-process=style/300';
					
				});
				this.productList = this.productList.concat(res.data);


			})
		},
		// 获取购物车
		loadData() {
			this.$xm.post('/Cart', '', res => {
				let goodsList = res.prolist;
				this.$xm.requestImg(goodsList);
				this.dataList = goodsList;
				this.count = res.count
				this.totalMoney = res.allprice;
				this.tips = res.tips;
				this.getmatchOptimals();
				if (this.count !== null) {
					this.count = this.count.toString()
					uni.setTabBarBadge({
						index: 2,
						text: this.count
					})
				} else {
					uni.removeTabBarBadge({
						index: 2,
					})
				}
				uni.setStorage({
					key: 'cart',
					data: this.count,
					success: (res) => {
						console.log(res);
					}
				})

			})
		},
		itemchange(e) {
			console.log(e);
		},
		checkboxChange(e) {
			this.allchecked = !this.allchecked;
			if (this.allchecked) {
				this.itemchecked = true;
			} else {
				this.itemchecked = false;
			}
		},
		changeNum(e) {
			this.delId = e.index;
			if (e.value == 0) {
				uni.showModal({
					title: '确定删除商品吗？',
					content: '',
					confirmText: '取  消',
					cancelColor: '#b2b2b2',
					confirmText: '确  定',
					confirmColor: '#2c2c2c',
					success: (res) => {
						if (res.confirm) {
							this.act = 'del';
							this.del();
						}
					},
				})
			} else {
				if (e.type == 'plus') {
					this.act = 'add';
					this.del()
				} else if (e.type == 'reduce') {
					this.act = 'reduce';
					this.del();
				}
			}


		},
		handlerButton(e) {
			this.delId = e.item.proid;
			this.act = 'del';
			this.del();
		},
		// 删除购物车
		del() {
			let params = {
				act: this.act,
				id: this.delId,

			}
			this.$xm.post('/Cart/ajax', params, res => {
				if (res.msg == '操作成功') {
					// this.loadData()();
					this.changeData()
				}
			})
		},
		// 加入购物车
		joinCart(e) {
			let params = {
				proid: e.id,
				pronum: 1,
				store:this.store
			};
			this.$xm.post('/Cart/add', params, (res) => {
				let result = res.msg;
				if (result == '加入购物车成功') {
					uni.showToast({
						title: "已加入购物车"
					});
					// this.loadData()();
					this.changeData()
				}
			})

		},
		delGoods: function() {

			this.isEdit = !this.isEdit;

		},
		detail(e) {
			uni.navigateTo({
				url: '../../goods/goods?gid=' + e.id
			});
		},
		topage() {
			uni.switchTab({
				url: '../category/category'
			})
		},
		btnPay() {
			console.log(this.store);
			uni.navigateTo({
				url: '../../order/confirmation?store='+this.store
			})
			// uni.navigateTo({
			// 	url: '../../order/confirmation'
			// })
		}
	},

	onPullDownRefresh() {
		// this.loadData()
		this.changeData()
		setTimeout(() => {
			uni.stopPullDownRefresh()
		}, 200)
	},
	onReachBottom() {
		this.changeProlist();
		setTimeout(() => {
			uni.stopPullDownRefresh()
		}, 200)

	},

}
